"use client";

import { useState } from "react";
import { toast } from "sonner";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

export default function RegisterPage() {
  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
    role: "PATIENT",
  });
  const [isLoading, setIsLoading] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const res = await fetch("http://localhost:4000/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(user),
      });

      const data = await res.json();

      if (res.ok) {
        toast.success("Inscription réussie !");
        window.location.href = "/login";
      } else {
        if (data.error) {
          if (typeof data.error === "object") {
            toast.error(
              Object.values(data.error)
                .map((e: any) => Object.values(e).join(", "))
                .join("\n")
            );
          } else {
            toast.error(data.error);
          }
        } else {
          toast.error("Erreur inscription");
        }
      }
    } catch (err) {
      toast.error("Erreur serveur");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="flex min-h-screen bg-blue-50">
      {/* Partie gauche */}
      <div className="hidden md:flex w-1/2 bg-blue-600 items-center justify-center">
        <img src="/doctors.png" alt="Doctors" className="w-3/4 h-auto" />
      </div>

      {/* Partie droite */}
      <div className="flex w-full md:w-1/2 items-center justify-center">
        <div className="bg-white p-12 rounded-3xl shadow-xl w-full max-w-lg">
          <h1 className="text-3xl font-bold text-blue-600 mb-6 text-center">Register</h1>
          <form className="space-y-4" onSubmit={handleSubmit}>
            <div>
              <label className="block text-blue-500 font-semibold mb-1">Full Name</label>
              <Input
                type="text"
                name="name"
                value={user.name}
                onChange={handleChange}
                placeholder="John Doe"
                required
              />
            </div>

            <div>
              <label className="block text-blue-500 font-semibold mb-1">Email</label>
              <Input
                type="email"
                name="email"
                value={user.email}
                onChange={handleChange}
                placeholder="example@mail.com"
                required
              />
            </div>

            <div>
              <label className="block text-blue-500 font-semibold mb-1">Password</label>
              <Input
                type="password"
                name="password"
                value={user.password}
                onChange={handleChange}
                placeholder="********"
                required
              />
            </div>

            <div>
              <label className="block text-blue-500 font-semibold mb-1">Role</label>
              <Select
                value={user.role}
                onValueChange={(value) => setUser({ ...user, role: value })}
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="Select a role" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="PATIENT">Patient</SelectItem>
                  <SelectItem value="DOCTOR">Doctor</SelectItem>
                  <SelectItem value="RECEPTIONIST">Receptionist</SelectItem>
                  <SelectItem value="ADMIN">Admin</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <Button type="submit" className="w-full mt-4" disabled={isLoading}>
              {isLoading ? "Création..." : "Register"}
            </Button>

            <div className="flex justify-center mt-4">
              <Button variant="outline" className="flex items-center gap-2">
                Create account with Google
                <img src="/Google.png" alt="Google Logo" className="w-5 h-5" />
              </Button>
            </div>

            <p className="text-center text-blue-900 mt-4">
              Already have an account?{" "}
              <a href="/login" className="font-bold underline">
                Login
              </a>
            </p>
          </form>
        </div>
      </div>
    </div>
  );
}
